import React from "react";

class Slide5 extends React.Component {
  render() {
    return (
      <div class="slide slide5">
        <img
          src={require("./img/music/ar_u_uf.jpg")}
          alt="U2 - The Unforgettable Fire"
          title="U2 - The Unforgettable Fire"
        />
      </div>
    );
  }
}

export default Slide5;



