import React from "react";

class Slide4 extends React.Component {
  render() {
    return (
      <div class="slide slide4">
        <img
          src={require("./img/music/ar_no_s.jpg")}
          alt="New Order - Substance"
          title="New Order - Substance"
        />
      </div>
    );
  }
}

export default Slide4;



