import React from "react";

class Slide2 extends React.Component {
  render() {
    return (
      <div class="slide slide2">
        <img
          src={require("./img/music/ar_f_ll.jpg")}
          alt="Frente - Labour of Love"
          title="Frente - Labour of Love"
        />
      </div>
    );
  }
}

export default Slide2;



